<template>
  <div class="container grid-md mt-2">
    <div class="columns">
      <!-- Form -->
      <div class="column col-12 mt-2">
        <div class="card">
          <div class="card-header">
            <div class="card-title h5">Instagram Grabber</div>
            <div class="card-subtitle text-gray">Put image or video from instagram posts.</div>
          </div>
          <div class="card-body">
            <div class="form-group">
              <label class="form-label" for="input-url">Enter URL</label>
              <div class="has-icon-right">
                <input class="form-input" type="text" id="input-url" :placeholder="form.url" v-model="form.url" @keyup.enter="validate">
                <i class="form-icon icon icon-link"></i>
              </div>
            </div>
          </div>
          <div class="card-footer">
            <button class="btn btn-primary mt-1" @click="validate">Proccess <i class="icon icon-search"></i></button>
          </div>
        </div>
      </div>
      <!-- Result -->
      <div class="column col-12 mt-2">
        <div class="card result">
          <div class="card-header">
            <div class="card-title h5">Result</div>
            <div class="card-subtitle text-gray">Result will be show here.</div>
          </div>
          <div class="card-body">
            <div v-if="err">
              <span class="label label-error">{{ err }}</span>
            </div>
            <div v-if="form.res != null">
              <p><span class="label">Username</span> : <span>{{ user.un }}</span></p>
              <p><span class="label">Caption</span> :
                <button class="btn btn-sm" @click="showCaptionTgl" v-if="!showCaption">Show <i class="icon icon-arrow-right"></i></button>
                <button class="btn btn-sm" @click="showCaptionTgl" v-if="showCaption">Hide <i class="icon icon-arrow-down"></i></button>
                <!-- caption -->
                <span v-if="showCaption">
                  <pre class="code"><code v-html="user.caption"></code></pre>
                </span>
              </p>
              <p class="label bg-warning">Click image to download.</p>
              <div v-for="data in form.res" :key="data">
                <div v-if="/.jpg/.test(data)">
                  <img class="img-responsive mt-1" :src="data" @click="downloadFile(data, 'jpg')">
                </div>
                <div v-if="/.mp4/.test(data)">
                  <video class="video-responsive mt-1" controls>
                    <source class="video-responsive" :src="data" type="video/mp4">
                    Your browser does not support the video tag.
                  </video>
                  <button class="btn btn-sm mt-1" @click="downloadFile(data, 'mp4')">Download video</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- copyright -->
    <p class="label label-rounded mt-2"><i class="icon icon-resize-horiz"></i> with <span class="text-error">♥</span> by <a class="tooltip" data-tooltip="Visit my github?" href="https://github.com/alfianokt" target="_blank">alfianokt</a></p>
  </div>
</template>


<script>
  import 'spectre.css'
  import 'spectre.css/dist/spectre-icons.css'
  import axios from 'axios'
  import FileDownload from 'js-file-download'
  // NProgress
  import NProgress from 'nprogress'
  import 'nprogress/nprogress.css'
  const cors = 'https://cors-masterkadal.herokuapp.com/'
  const copyright = 'alfianokt'

  // https://www.instagram.com/p/B4MzFI5FAe9/?utm_source=ig_web_copy_link
  // https://www.instagram.com/p/B4OZcQ-jbEq/?utm_source=ig_web_copy_link
  export default {
    data(){
      return {
        form: {
          url: null,
          res: null
        },
        err: null,
        download: false,
        proccess: false,
        showCaption: false,
        user: {
          un: null,
          caption: null
        }
      }
    },
    methods: {
      resetUser: function (){
        this.user.un, this.user.caption, this.form.res = null
      },
      validate: function (){
        if (/[http|https]:\/\/www.instagram.com\/p\/([\w]+)/.test(this.form.url)){
          this.proccessLink()
        } else {
          this.err = "Please give me valid instagram post link."
          setTimeout(() => {
            this.err = null
          }, 3000)
        }
      },
      proccessLink: async function (){
        NProgress.start()
        this.err = null
        try {
          var get = await axios.get( cors + this.form.url)

          // search image
          var match = get.data.match(/display_url":"(.+?)"/g)
          match.length > 1 && match[0] == match[1] ? match.splice(0,1) : ''
          match.forEach((data, i)=>{
            match[i] =  data.match(/display_url":"(.+?)"/)[1].replace(/\\u0026/, '&')
          })

          // search video
          var matchVideo = get.data.match(/video_url":"(.+?)"/gi)
          if(matchVideo != null && matchVideo.length > 0) {
            matchVideo.forEach((data)=>{
              let video =  data.match(/video_url":"(.+?)"/)[1].replace(/\\u0026/, '&')
              match.push(video)
            })
          }

          NProgress.done()
          this.form.res = match
          var un = get.data.match(/"username":"(.+?)"/g)
          un = un[un.length - 1].match(/"username":"(.+?)"/)[1]
          this.user.un = un
          var cap = get.data.match(/"edge_media_to_caption":{"edges":\[{"node":{"text":"(.+?)"}/)
          this.user.caption = decodeURIComponent(escape(cap[0].match(/"edge_media_to_caption":{"edges":\[{"node":{"text":"(.+?)"/)[1].replace(/\\n/g, '')))
        }
        catch (err) {
          NProgress.done()
          this.err = err
          setTimeout(() => {
            this.err = null
          }, 3000)
          this.resetUser()
        }
      },
      randomStr: function (length) {
        var result           = '';
        var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        var charactersLength = characters.length;
        for ( var i = 0; i < length; i++ ) {
            result += characters.charAt(Math.floor(Math.random() * charactersLength));
        }
        return result;
      },
      downloadFile: function (url, type){
        NProgress.start()
        axios.get(url, {responseType: 'arraybuffer'})
          .then((response) => {
            // NProgress.done()
            FileDownload(response.data, this.user.un + '_' +this.randomStr(4)+ ' - '+copyright+'.'+type);
          })
          .error( err => {
            alert(err)
          })
          .finally(() => {
            NProgress.done()
          })
      },
      showCaptionTgl: () => {
        this.showCaption = !this.showCaption
      }
    }
  }
</script>

<style scoped>
  .result {
    min-height: 50vh;
  }
</style>
